<template>
  <div v-if="student">
    <v-row>
      <v-col md="6" cols="12">
        <v-card raised shaped dark>
          <v-card-title>
            <v-row class="text-left">
              <v-col cols="12">
                <v-icon class="mx-3">mdi-account</v-icon>
                <span class="font-weight-light">Name:</span>
                <strong>
                  {{ student.firstName }} {{ student.middleName }}
                  {{ student.lastName }}</strong
                >
              </v-col>
              <v-col cols="12">
                <v-icon class="mx-3">mdi-medal</v-icon>
                <span class="font-weight-light">Ranking:</span>
                <strong> {{ ranking.rank }} of {{ ranking.size }} </strong>
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
      <v-col md="6" cols="12">
        <v-card raised shaped dark>
          <v-card-title>
            <v-row class="text-left">
              <v-col cols="12">
                <v-icon class="mx-3">mdi-book</v-icon>
                <span class="font-weight-light">Homework Ratio:</span>
                <strong>
                  {{ completedAssignmentCount }} of
                  {{ assignmentCount }}
                </strong>
              </v-col>
              <v-col cols="12">
                <v-icon class="mx-3">mdi-calculator</v-icon>
                <span class="font-weight-light">Homework Average:</span>
                <strong
                  :class="{
                    'green--text': average > 50,
                    'red--text': average < 30,
                    'amber--text': average > 30 && average < 50
                  }"
                >
                  {{ average }} %
                </strong>
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
    </v-row>
    <v-col cols="4"></v-col>
    <v-row>
      <v-col md="4" cols="12">
        <v-card>
          <v-card-text>
            <v-select
              label="Enrolled"
              outlined
              :item-text="item => selectEnrolledText(item)"
              item-value="index"
              v-model="selectedEnroll"
              :items="enrolleds"
            >
            </v-select>
            <chartist
              class="ct-chart"
              ratio="ct-major-second"
              type="Line"
              :data="assignmentChartData"
              :options="{
                low: 0,
                high: 100
              }"
            >
            </chartist>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col md="4" cols="12">
        <v-card>
          <v-card-text>
            <chartist
              v-if="selectedEnroll"
              class="ct-chart"
              ratio="ct-major-second"
              type="Bar"
              :data="enrolledChartData"
              :options="{
                high: 100,
                low: 0
              }"
            >
            </chartist>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <!-- <v-row>
      <v-col
        cols="12"
        lg="3"
        md="4"
        sm="6"
        v-for="(enrolled, index) in enrolleds"
        :key="index"
      >
        <v-card raised shaped dark>
          <v-row class="ml-3 text-left">
            <v-col cols="12">
              <v-icon class="mr-3">mdi-book</v-icon>
              {{ enrolled }}
            </v-col>
            <v-col cols="12">
              <v-icon class="mr-3">mdi-account</v-icon>
              {{ enrolled.teacher.firstName }}
            </v-col>
            <v-col cols="12">
              <v-icon class="mr-3">mdi-calculator</v-icon>
              {{ enrolled.average }} %
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row> -->
  </div>
</template>

<script>
import StudentService from "../services/StudentService";

export default {
  data() {
    return {
      enrolleds: [],
      assignments: [],
      assignmentChartData: {},
      enrolledChartData: {},
      selectedEnroll: Number,
      studentId: Number,
      student: Object,
      average: Number,
      ranking: Number,
      assignmentCount: 0,
      completedAssignmentCount: 0,
      assignmentMarks: []
    };
  },

  created() {
    this.studentId = this.$route.params.studentId;
    StudentService.GetStudent(this.studentId).then(student => {
      this.student = student;
      StudentService.GetEnrolleds(this.studentId).then(enrolleds => {
        this.enrolleds = enrolleds;
        if (enrolleds.length > 0) {
          this.selectedEnroll = 0;
        }
      });
      StudentService.GetStudentAssignments(this.studentId).then(assignments => {
        this.assignments = assignments;
      });
    });
  },

  methods: {
    selectEnrolledText(item) {
      let string = "";

      string = string.concat(
        item.Offer.Course.name,
        " ",
        item.Offer.code,
        " ",
        item.Offer.Teacher.firstName + " " + item.Offer.Teacher.lastName
      );

      return string;
    }
  },

  watch: {
    selectedEnroll: function() {
      let labels = [];
      let series = [[]];

      this.enrolleds[this.selectedEnroll].Assignments.forEach(element => {
        series[0].push(element.mark);
        labels.push(element.Homework.name);
      });

      this.assignmentChartData = { labels, series };
    },

    enrolleds: function() {
      for (let i = 0; i < this.enrolleds.length; i++) {
        this.$set(this.enrolleds[i], "index", i);
      }
    }
  }
};
</script>

